<template>
	<view>
	
		<view class="log_pic">
		<image src="../../static/snow.jpg" mode=""></image>	
		</view>
		<view class="out">
			
			<view class="row">
				<input  class="border" type="text" placeholder="请输入用户名" v-model="message.username">
			</view>
			<view class="row">
				<input type="text" class="border"  placeholder="请输入账号" v-model="message.tel">
			</view>
			<view class="row" >
				<input type="text" class="border" placeholder="请输入密码" v-model="message.code">
			</view>
			
		</view >
			
		<view class="buttonregister">
			<button size="mini" type="primary" @click="register">注册</button>
		</view>
      <view class="buttonlog">
			<button size="mini" type="primary" @click="login" >登录</button>
		</view>
		
	</view>
</template>

<script>
	export default {
		data() {
			return {
				title:"asd",
				message:{
					username:"",
					code:"",
					tel:"",
					content:""
				}
			}
		},
		methods: {
			login(){
				if (!this.message.username) {
					        uni.showToast({
					            title: "用户名不能为空！",
					            icon: "none",
					            duration: 2000
					        });
					        return;
					    }
					    if (!this.message.tel) {
					        uni.showToast({
					            title: "账号不能为空！",
					            icon: "none",
					            duration: 2000
					        });
					        return;
					    }
					    if (!this.message.code) {
					        uni.showToast({
					            title: "密码不能为空！",
					            icon: "none",
					            duration: 2000
					        });
					        return;
					    }
				   
				        uni.switchTab({
				        	url: "/pages/log/log"
						
				        })	
				},
				onsubmit(){
					console.log(this.message)
				},
				onclick(){
					this.title=Math.random()
				},
				comeBack(){
					uni.navigateBack()
				},
			
	register() {
			    uni.showToast({
			        title: "正在加载",
			        icon: "loading",
			        mask: true,
			    });
			    setTimeout(() => {
			        uni.navigateTo({
			            url: "/pages/register/register"
			        });
			    }, 500);
				
			},
			onsubmit(){
				console.log(this.message)
			},
			onclick(){
				this.title=Math.random()
			},
			comeBack(){
				uni.navigateBack()
			}
			
		}
	}
</script>

<style lang="scss">
input{
	border: 1px solid #ccc;
}
.out{
	padding: 30rpx;
	.row{
		margin-bottom: 10px;
	}
	.border{
	 border: 1px solid #eee;
	 width: 100%;
	 min-height: 60rpx;
	 padding: 0 20rpx;
	 box-sizing:border-box;
}
}
.log_pic {
    display: flex;
    justify-content: center;
    align-items: center;
}

.buttonlog, .buttonregister {
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

button {
    width: 80%;
    padding: 10px 0;
    font-size: 18px;
    border-radius: 5px;
}
</style>